import React from 'react'

class AccessibleFAIcon extends React.Component {
  static propTypes = {
    animations: React.PropTypes.array,
    icon: React.PropTypes.string.isRequired,
    size: React.PropTypes.string,
  }

  static readerTextStyle = {
    clip: 'rect(1px, 1px, 1px, 1px)',
    position: 'absolute !important',
    height: '1px',
    width: '1px',
    overflow: 'hidden',
  }

  static wrapStyle = {
    display: 'inline-block',
  }

  render() {
    const props = this.props
    const size = props.size || ''
    const anims = props.animations.join(' ')
    const text = props.children || props.icon

    return (
      <span style={this.wrapStyle}>
        <span style={this.readerTextStyle}>
          {text}
        </span>
        <i aria-hidden={true} className={`fa ${props.icon} ${size} ${anims}`} />
      </span>
    )
  }
}

export default AccessibleFAIcon
